import base_styles from 'common/css/base.module.scss';
import PageSection from '../PageSection/PageSection';
import styles from './style.module.scss';
import Typed from 'react-typed';

import profImg from 'common/imgs/top/img-myprof.jpg';

const About = () => {
  return (
    <PageSection
      sectionId='about'
      bg_color='white'
      sectionTtl="About me"
      sectionUnderTtl=""
    >
      <div className={styles.wrap}>
        <div className={styles.left} data-aos="fade-in">
          <img src={profImg} alt="" />
        </div>
        <div className={styles.right}>
          <h3>
            <span className={styles.typed}>
            <Typed 
              strings={["Fumihito Sudo", "", " "]}
              typeSpeed={100}
              startDelay={0}
              backSpeed={50}
              backDelay={750}
              loop={true}
              cursorChar={"|"}
            />
            </span>
          </h3>
          <span className={styles.word}></span>
          <span className={styles.word}>2002</span>
          <p>~2019<a href="https://www.japan-mba.org/" target="_blank" rel="noreferrer noopener"></a></p>
          <p>2020<span></span><span></span><br />
            webweb
          </p>
          <div className={styles.icons}>
            <a href="https://github.com/fum1h1to" target="_blank" rel="noreferrer noopener"><i className={`${styles.github} fab fa-brands fa-github`}></i></a>
            <a href="mailto:Engineer.FumiM@gmail.com"><i className={`${styles.mail} far fa-envelope`}></i></a>
          </div>
        </div>
      </div>
    </PageSection>
  );
}

export default About;